.title {
    margin-top: 20px;
    color: white;
}
.threadDownload {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100vh;
    background: url("../../public/image/background.png") no-repeat;
    background-position: center center;
}
.download {
    display: flex;
    padding: 10px 2.5rem;
    color: black;
    text-decoration: none;
    background: rgb(225, 172, 230);
    border-radius: 1rem;
    transition: all .35s;
}
.infomation {
    color: white;
    font-weight: 600;
    text-transform: uppercase;
    font-size: 20px;
    margin-bottom: 200px;
    margin-left: 50px;
}
.download:hover {
    background: rgb(164, 127, 167);
}
.threadDownload h1 {
    font-size: 40px;
    font-weight: 600;
}
.parentdownload {
    margin-top: 20px;
    display: flex;
    align-content: center;
    justify-content: center;
}
.warning {
    color: black;
    font-weight: 600;
    background: rgb(233, 153, 153);
    border: 1px solid black;
    border-radius: 1rem;
    padding: 1rem;
    margin: 20px 40px;
}
.warningparent {
    display: flex;
    justify-content: center;
    width: 100%;
}
.moreInfo {
    margin: 40px 20px;
}
.changelog {
    background: rgba(102, 194, 115, 0.2);
    min-height: 700px;
    max-width: 600px;
    border-radius: 1rem;
    width: 100vw;
    margin: 0 50px;
    padding: 10px 20px;
}
.parentChangelog {
    display: flex;
    justify-content: center;
    margin-bottom: 50px;
}
.parentChangelog h2 {
    color: rgb(0, 0, 0);
    text-align: center;
}
body {
    background: rgb(233, 242, 245);
}